import React, { useEffect, useRef, useState } from "react";
import ReactQuill from "react-quill";
import "./Editor.css";
import "react-quill/dist/quill.snow.css";
import "../assets/quill.css";
// import Parchment from 'parchment'
import ImageResize from "quill-image-resize-module";

ReactQuill.Quill.register("modules/ImageResize", ImageResize);
let font = ReactQuill.Quill.import("formats/font");
font.whitelist = [
  "NanumGothic",
  "NanumGothicCoding",
  "NanumMyeongjo",
  "MalgunGothic",
  "NotoSans",
  "NotoSerif"
];
ReactQuill.Quill.register(font, true);
var FontAttributor = ReactQuill.Quill.import("attributors/class/font");
FontAttributor.whitelist = [
  "NanumGothic",
  "NanumGothicCoding",
  "NanumMyeongjo",
  "MalgunGothic",
  "NotoSans",
  "NotoSerif",
  "serif"
];
ReactQuill.Quill.register(FontAttributor, true);

const CustomToolbar = ({ onClickRaw }) => (
  <div id="toolbar">
    <span className="ql-formats">
      <button className="ql-bold"></button>
      <button className="ql-italic"></button>
      <button className="ql-underline"></button>
      <button className="ql-strike"></button>
    </span>
    <span className="ql-formats">
      <button className="ql-blockquote"></button>
      <button className="ql-code-block"></button>
    </span>
    <span className="ql-formats">
      <select className="ql-header">
        <option value="1"></option>
        <option value="2"></option>
        <option value="3"></option>
        <option value="4"></option>
        <option value="5"></option>
        <option value="6"></option>
        <option defaultValue></option>
      </select>
    </span>
    <span className="ql-formats">
      <button className="ql-list" value="ordered"></button>
      <button className="ql-list" value="bullet"></button>
    </span>
    <span className="ql-formats">
      <button className="ql-script" value="sub"></button>
      <button className="ql-script" value="super"></button>
    </span>
    <span className="ql-formats">
      <button className="ql-indent" value="-1"></button>
      <button className="ql-indent" value="+1"></button>
    </span>
    <span className="ql-formats">
      <select className="ql-font" title="Font">
        <option selected>Normal</option>
        <option className="NanumMyeongjo" value="NanumMyeongjo">
          나눔명조
        </option>
        <option className="MalgunGothic" value="MalgunGothic">
          맑은고딕
        </option>
        <option className="NotoSans" value="NotoSans">
          본고딕
        </option>
        <option className="NotoSerif" value="NotoSerif">
          본명조
        </option>
        <option className="serif" value="serif"></option>
        {/* <option defaultValue></option> */}
      </select>
      <select className="ql-color"></select>
      <select className="ql-background"></select>
    </span>

    <span className="ql-formats">
      <select className="ql-align"></select>
    </span>
    <span className="ql-formats">
      <button className="ql-link"></button>
      <button className="ql-image"></button>
      <button className="ql-video"></button>
    </span>
    <span className="ql-formats">
      <button className="ql-showhtml" onClick={onClickRaw}>
        <i className="material-icons">code</i>
        {/* Raw */}
      </button>
    </span>
  </div>
);

const Quill = props => {
  const { value, setValue, placeholder } = props;
  const [rawHtml, setRawHtml] = useState("");
  const [showRaw, setShowRaw] = useState(false);

  useEffect(() => {
    console.log(value);
    setRawHtml(value.detail);
  }, [value]);

  const handleChangeRaw = html => {
    setRawHtml(html);
    setValue({ ...value, detail: html });
  };

  const handleClickShowRaw = () => {
    if (showRaw === true) {
      setValue({ ...value, detail: rawHtml });
    }
    const isEditingRaw = showRaw;

    setShowRaw(!isEditingRaw);
    syncViews(isEditingRaw);
  };

  const syncViews = fromRaw => {
    if (fromRaw) setValue({ ...value, detail: rawHtml });
    else setRawHtml(value.detail);
  };

  const modules = {
    // font,
    ImageResize: {
      displayStyles: {
        backgroundColor: "black",
        border: "none",
        color: "white"
      },
      modules: ["Resize", "DisplaySize", "Toolbar"]
    },
    toolbar: {
      container: "#toolbar",
      handlers: {}
    }
  };

  const formats = [
    "header",
    "bold",
    "italic",
    "underline",
    "strike",
    "blockquote",
    "code-block",
    "list",
    "bullet",
    "script",
    "indent",
    "link",
    "image",
    "size",
    "font",
    "color",
    "background",
    "code",
    "display",
    "width",
    "height",
    "float",
    "align",
    "family"
  ];

  return (
    <div>
      <CustomToolbar onClickRaw={handleClickShowRaw} />
      <ReactQuill
        id="editor"
        theme="snow"
        modules={modules}
        className="add-new-post__editor mb-1"
        placeholder={placeholder}
        onChange={handleChangeRaw}
        value={rawHtml || ""}
        name="detail"
        formats={formats}
      ></ReactQuill>
    </div>
  );
};

export default Quill;
