import React, { useEffect, useRef, useState } from "react";
import { Button, Card, CardBody, Col, FormCheckbox, FormInput, FormSelect, FormTextarea, InputGroup, Modal, ModalBody, ModalHeader, Row } from "shards-react";
import './Editor.css';
import constants from "../../api/constant"
// import Parchment from 'parchment'


const Editor = props => {

  const { value, setValue } = props;
  const [rawHtml, setRawHtml] = useState('');
  const [showRaw, setShowRaw] = useState(false);
  const [category, setCategory] = useState(0);



  useEffect(() => {
    console.log(value)
    setRawHtml(value.detail)
  }, [value])


  const handleChangeRaw = (html) => {
    setRawHtml(html);
    setValue({ ...value, detail: html })


  }
  const handleClickShowRaw = () => {
    if (showRaw === true) {
      setValue({ ...value, detail: rawHtml })
    }
    const isEditingRaw = showRaw;


    setShowRaw(!isEditingRaw)
    syncViews(isEditingRaw)
  }

  const syncViews = (fromRaw) => {
    if (fromRaw) setValue({ ...value, detail: rawHtml })
    else setRawHtml(value.detail)
  }

  const onChange = e => {
    const {
      target: { value: nValue, name }
    } = e;

    const newValue = { ...value };
    newValue[name] = nValue;

    setValue(newValue)
  };
  const changeCategory = (e) => {

    const { target: { value: nValue } } = e;

    setCategory(nValue)

  }

  return (
    <Card small className="mb-3">
      <CardBody>
        {/* <Form className="add-new-post"> */}

        <Row>
          <Col md="12" className="form-group">
            <label htmlFor="title">상품명</label>
            <FormInput
              id="title"
              size="lg"
              name="title"
              placeholder="Your Post Title"
              value={value.title}
              onChange={onChange}

            />
          </Col>
        </Row>
        <Row>
          <Col md="6" className="form-group">
            <label htmlFor="price">가격</label>
            <FormInput
              id="price"
              name="price"
              type="number"
              placeholder="가격을 기입해주세요."
              value={value.price}
              onChange={onChange}
            />
          </Col>
          <Col md="6" className="form-group">
            <label htmlFor="productCode">자체상품코드</label>
            <FormInput
              id="productCode"
              name="productCode"
              type="number"
              placeholder="상품코드를 기입해주세요."
              value={value.productCode}
              onChange={onChange}
            />
          </Col>

        </Row>
        <Row>
          <Col md="6" className="form-group">
            <label htmlFor="sojae">소재</label>
            <FormInput
              id="sojae"
              type="text"
              placeholder="소재 정보를 입력해주세요."
              name="sojae"
              value={value.sojae}
              onChange={onChange}
            />
          </Col>
          <Col md="6" className="form-group" >
            <label htmlFor="fit">핏</label>
            <FormInput
              id="fit"
              type="text"
              placeholder="옷의 핏감을 입력해주세요"
              name="fit"
              value={value.fit}
              onChange={onChange}
            />

          </Col>
        </Row>
        <Row>
          <Col md="6" className="form-group">
            <label htmlFor="setak">세탁정보</label>
            <FormInput
              id="setak"
              type="text"
              name="setak"
              placeholder="세탁정보를 입력해주세요."
              value={value.setak}
              onChange={onChange}
            />
          </Col>
          <Col md="6" className="form-group" >
            <label htmlFor="model">모델정보</label>
            <FormInput
              id="model"
              name="model"
              type="text"
              placeholder="모델 정보를 입력해주세요."
              value={value.model}
              onChange={onChange}
            />

          </Col>
        </Row>
        <Row>
          <Col md="6" className="form-group" >
            <label htmlFor="mainCategory">주 카테고리</label>
            <FormSelect
              id="mainCategory"
              name="mainCategory"
              onChange={changeCategory}
            >
              {
                constants.mainCategory.map(item => (
                  <option key={item.id} value={item.id}>{item.name}</option>
                ))
              }
            </FormSelect>

          </Col>
          <Col md="6" className="form-group" >
            <label htmlFor="categoryId">세부 카테고리</label>
            <FormSelect
              id="categoryId"
              name="categoryId"
              onChange={onChange}
            >
              {
                constants.mainCategory[category].subCategory.map(item => (
                  <option key={item.id} value={item.id}>{item.name}</option>
                ))
              }

            </FormSelect>

          </Col>
          <Col md="12" className="form-group" >
            <label htmlFor="detail">상품 간략설명</label>
            <FormTextarea
              id="detail"
              name="detail"
              type="text"
              placeholder="상품 간략 소개."
              value={value.detail}
              onChange={onChange}
            />

          </Col>

        </Row>

        <Modal size="lg" open={showRaw} centered={true}>
          <ModalHeader className="d-block">
            test
            {/* {value.title.slice(0, 40) + (value.title.length > 40 ? "..." : "")} */}
            <div
              onClick={() => {
                handleClickShowRaw()
              }}
              style={{
                cursor: "pointer",
                position: "absolute",
                right: "1rem",
                top: "1rem"
              }}
            >
              <i className="material-icons">close</i>
            </div>
          </ModalHeader>
          <ModalBody>
            <InputGroup className="mb-3">
              <FormTextarea style={{ height: "70vh" }} size="lg" onChange={(e) => (handleChangeRaw(e.target.value))} value={rawHtml} />
            </InputGroup>
            <Button style={{ marginLeft: "calc(100% - 1.875rem - 28px)" }} onClick={handleClickShowRaw}>완료</Button>
          </ModalBody>
        </Modal>
        {/* </Form> */}
      </CardBody>
    </Card>
  );
};

export default Editor;
