import React, { useEffect, useState } from "react";
import {
    Container,
    Row,
    Col,
    Card,
    CardHeader,
    CardBody,
    Button,
    Modal,
    ModalHeader,
    ModalBody,
    ModalFooter,
    Form,
    FormInput,
    FormTextarea
} from "shards-react";

import PageTitle from "../components/common/PageTitle";
import api from "../api/api";
import Pagination from "../components/inquiry/pagination";
import Quill from "../components/Quill";
import "./Partner.css";

const Partner = () => {
    const [modalOpen, setModalOpen] = useState();
    const [modalData, setModalData] = useState({ User: {} });
    const [partners, setPartners] = useState([])
    const [imageFile, setImageFile] = useState()
    const [image, setImage] = useState("");


    useEffect(async () => {
        const res = await api.getPartners()
        setPartners(res);
    }, []);

    const onChange = item => () => {
        setModalData(item);

        setModalOpen(prev => !prev);
    };

    const modalOnChange = (e) => {
        const { name, value } = e.target;
        setModalData(prev => {
            const newData = { ...prev }
            newData[name] = value
            return newData
        })
    }

    const encodeFileToBase64 = (fileBlob) => {
        const reader = new FileReader();
        reader.readAsDataURL(fileBlob);
        return new Promise((resolve) => {
            reader.onload = () => {
                setImage(reader.result);
                resolve();
            };
        });
    }
    const dataURLtoFile = (dataurl, fileName) => {

        var arr = dataurl.split(','),
            mime = arr[0].match(/:(.*?);/)[1],
            bstr = atob(arr[1]),
            n = bstr.length,
            u8arr = new Uint8Array(n);

        while (n--) {
            u8arr[n] = bstr.charCodeAt(n);
        }

        return new File([u8arr], fileName, { type: mime });
    }


    return (
        <Container fluid className="main-content-container px-4">
            {/* Page Header */}
            <Row noGutters className="page-header py-4">
                <PageTitle
                    sm="4"
                    title="파트너 관리"

                    className="text-sm-left"
                />
            </Row>
            <Button
                onClick={() => { setModalOpen(true) }}
            >
                등록하기
            </Button>

            {/* Default Light Table */}
            <Row>
                <Col>
                    <Card
                        small
                        className="mb-4"
                        style={{ position: "relative", height: "650px" }}
                    >
                        <CardHeader className="border-bottom">
                            <h6 className="m-0">파트너 목록</h6>
                        </CardHeader>
                        <CardBody className="p-0 pb-5">
                            <table className="table mb-0" style={{ tableLayout: 'fixed' }}>
                                <thead className="bg-light">
                                    <tr>
                                        <th scope="col" className="border-0" width="5%" style={{ paddingLeft: "30px" }}>
                                            #
                                        </th>
                                        <th scope="col" className="border-0" width="5%">
                                            파트너 이름
                                        </th>
                                        <th scope="col" className="border-0" width="10%">
                                            제목
                                        </th>
                                        <th scope="col" className="border-0" width="10%">
                                            소속
                                        </th>
                                        <th scope="col" className="border-0" width="30%">
                                            경력사항
                                        </th>
                                        <th scope="col" className="border-0" width="20%" >
                                            인스타링크
                                        </th>
                                        <th scope="col" className="border-0" width="20%">
                                            유튜브 링크
                                        </th>

                                    </tr>
                                </thead>
                                <tbody>
                                    {partners.map((item, index) => (
                                        <tr
                                            style={{ cursor: "pointer" }}
                                            onClick={onChange(item)}
                                            key={item.id}
                                        >

                                            <td style={{ paddingLeft: "30px" }}>{item.id}</td>
                                            <td style={{ whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden" }}>{item.name}</td>
                                            <td style={{ whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden" }}>{item.title}</td>
                                            <td style={{ whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden" }}>{item.role}</td>
                                            <td style={{ whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden" }}>{item.contents}</td>
                                            <td style={{ whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden" }}>{item.instagram}</td>
                                            <td style={{ whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden" }}>
                                                {item.youtube}
                                            </td>

                                        </tr>
                                    ))}
                                </tbody>
                                <Modal open={modalOpen} centered={true} size="lg">
                                    <ModalHeader className="d-block">
                                        파트너 관리
                                        <div
                                            onClick={onChange({})}
                                            style={{
                                                cursor: "pointer",
                                                position: "absolute",
                                                right: "1rem",
                                                top: "1rem"
                                            }}
                                        >
                                            <i className="material-icons">close</i>
                                        </div>
                                    </ModalHeader>
                                    <ModalBody>
                                        <Row>
                                            <Col lg={9}>
                                                <Form>
                                                    <label htmlFor="seksang">이름</label>
                                                    <FormInput
                                                        id="name"
                                                        type="text"
                                                        name="name"
                                                        placeholder="파트너 이름을 입력하세요"
                                                        value={modalData.name}
                                                        onChange={modalOnChange}
                                                    />
                                                    <label htmlFor="title">제목</label>
                                                    <FormInput
                                                        id="title"
                                                        type="text"
                                                        className="mb-3"
                                                        name="title"
                                                        placeholder="파트너 타이틀을 입력하세요"
                                                        value={modalData.title}
                                                        onChange={modalOnChange}
                                                    />
                                                    <label htmlFor="role">소속</label>
                                                    <FormInput
                                                        id="role"
                                                        type="text"
                                                        className="mb-3"
                                                        name="role"
                                                        placeholder="파트너 소속을 입력하세요"
                                                        value={modalData.role}
                                                        onChange={modalOnChange}
                                                    />
                                                    <label htmlFor="contents">경력사항</label>
                                                    <FormTextarea
                                                        id="contents"
                                                        className="mb-3"
                                                        name="contents"
                                                        placeholder="파트너 경력사항을 입력하세요"
                                                        value={modalData.contents}
                                                        onChange={modalOnChange}
                                                    />
                                                    <label htmlFor="instagram">인스타그램</label>
                                                    <FormInput
                                                        id="instagram"
                                                        type="text"
                                                        className="mb-3"
                                                        name="instagram"
                                                        placeholder="파트너 인스타 프로필 링크를 입력하세요"
                                                        value={modalData.instagram}
                                                        onChange={modalOnChange}
                                                    />
                                                    <label htmlFor="youtube">유튜브</label>
                                                    <FormInput
                                                        id="youtube"
                                                        type="text"
                                                        className="mb-3"
                                                        name="youtube"
                                                        placeholder="파트너 유튜브 프로필 링크를 입력하세요"
                                                        value={modalData.youtube}
                                                        onChange={modalOnChange}
                                                    />
                                                </Form>
                                            </Col>
                                            <Col lg={3}>
                                                이미지 등록
                                                <div>
                                                    {
                                                        image ?
                                                            (<img style={{ width: "100%", objectFit: "cover" }} src={image} alt="partner-picture"></img>) :
                                                            (<img style={{ width: "100%", objectFit: "cover" }} src={modalData.image} alt="partner-picture"></img>)
                                                    }
                                                    <FormInput
                                                        onChange={(e) => {
                                                            setImageFile(e.target.files[0])
                                                            encodeFileToBase64(e.target.files[0])
                                                        }}
                                                        type="file"
                                                        src="./assets/sample.png"
                                                        accept=".jpg, .jpeg, .png"
                                                        alt="제출버튼"
                                                    />




                                                </div>
                                            </Col>
                                        </Row>
                                    </ModalBody>
                                    <ModalFooter>
                                        <Button theme="secondary" onClick={() => { setModalOpen(false) }}>
                                            취소
                                        </Button>
                                        <Button
                                            onClick={async () => {
                                                const formData = new FormData();
                                                formData.append('name', modalData.name)
                                                formData.append('title', modalData.title)
                                                formData.append('role', modalData.role)
                                                formData.append('contents', modalData.contents)
                                                formData.append('image', imageFile)
                                                formData.append('instagram', modalData.instagram)
                                                formData.append('youtube', modalData.youtube)
                                                if (modalData.id) {

                                                    const res = await api.patchPartner(modalData.id, formData)
                                                    console.log(res);
                                                } else {
                                                    const res = await api.postPartners(formData)
                                                    console.log(res);

                                                }

                                                setPartners(await api.getPartners())
                                                setModalOpen(false)



                                            }}
                                        >
                                            {!!modalData.reply ? "수정하기" : "등록하기"}
                                        </Button>

                                    </ModalFooter>
                                </Modal>
                            </table>
                        </CardBody>
                        <Pagination
                            style={{
                                display: "flex",
                                position: "absolute", bottom: 10,
                                justifyContent: 'center'
                            }}
                        />
                    </Card>
                </Col>
            </Row>
        </Container>
    );
};

export default Partner;
