import Quill from "../components/Quill";
import React, { useEffect, useState } from "react";
import {
  ButtonGroup,
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Container,
  FormInput,
  Row
} from "shards-react";
import PageTitle from "../components/common/PageTitle";
import api, { BASE_URL } from "../api/api";
import Compressor from "compressorjs";

function AddProductDetail({ location, history }) {
  const [detail, setDetail] = useState({});
  const [color, setColor] = useState({ Product: {} });

  const [imageFile, setImageFile] = useState(null);
  const [detailImageFile, setDetailImageFile] = useState(null);

  const [image, setImage] = useState(null);
  const [detailImages, setDetailImages] = useState([]);

  const [descriptionImages, setDescriptionImages] = useState([]);
  const [descriptionImageFiles, setDescriptionImageFiles] = useState(null);

  const IMAGE_URL = `${BASE_URL}public/products/${location.state.colorId}/`;

  const RESIZE_OPTION = {
    maxSizeMB: 1
  };

  useEffect(async () => {
    const res = await api.getOneColor(location.state.colorId);
    console.log(res);
    if (!res) {
      return;
    }
    setColor(res);
    // 자료 받아오기
    setDetail({ detail: res.detail });

    setImage(`${IMAGE_URL}thumb/${res.thumb}`);

    const displaies = res.ProductDisplays.map(item => {
      // 대체이미지 불러오기
      return `${IMAGE_URL}displaies/${item.image}`;
    });
    setDetailImages(displaies);

    const images = res.ProductImages.map(item => {
      // 상세이미지 불러오기
      return `${IMAGE_URL}images/${item.image}`;
    });
    setDescriptionImages(images);
  }, []);

  const onFileChange = e => {
    var files = e.target.files || e.dataTransfer.files;
    if (!files.length) return;
    new Compressor(files[0], {
      quality: 0.8,
      success: result => {
        setImageFile(result);
      }
    });
    createImage(files[0]);
  };

  const onMulitpleFileChange = async e => {
    setDetailImageFile(null);
    setDetailImages([]);
    var files = e.target.files || e.dataTransfer.files;
    const imageArray = [];

    if (!files.length) return;
    for (let i = 0; i < files.length; i++) {
      new Compressor(files[0], {
        quality: 0.8,
        success: result => {
          imageArray.push(result);
        }
      });
      createMulipleImage(files[i]);
    }
    setDetailImageFile(imageArray);
  };

  const createMulipleImage = file => {
    var reader = new FileReader();
    reader.onload = e => {
      setDetailImages(old => [...old, e.target.result]);
    };
    reader.readAsDataURL(file);
  };

  const onMulitpleDetailChange = async e => {
    setDescriptionImageFiles(null);
    setDescriptionImages([]);
    var files = e.target.files || e.dataTransfer.files;

    const imageArray = [];

    if (!files.length) return;
    for (let i = 0; i < files.length; i++) {
      new Compressor(files[0], {
        quality: 0.8,
        success: result => {
          imageArray.push(result);
        }
      });
      createMulipleDetailImage(files[i]);
    }
    setDescriptionImageFiles(imageArray);
  };

  const createMulipleDetailImage = file => {
    var reader = new FileReader();
    reader.onload = e => {
      setDescriptionImages(old => [...old, e.target.result]);
    };
    reader.readAsDataURL(file);
  };

  const createImage = file => {
    var reader = new FileReader();

    reader.onload = e => {
      setImage(e.target.result);
    };
    reader.readAsDataURL(file);
  };
  const removeImage = () => {
    setImage(null);
  };

  const removeMultiFile = idx => {
    setDetailImages(detailImages.filter(item => item.id !== idx));
  };

  const getThumbFile = async imageFile => {
    const fr = new FileReader();
    let resizedImageFile;

    fr.onload = base64 => {
      const image = new Image();

      image.src = base64.target.result;
      image.onload = e => {
        const canvas = document.createElement("canvas");
        const ctx = canvas.getContext("2d");

        canvas.width = e.target.width;
        canvas.height = e.target.height;

        ctx.drawImage(e.target, 0, 0);

        var dataURI = canvas.toDataURL("image/png"); //png => jpg 등으로 변환 가능
        var byteString = atob(dataURI.split(",")[1]);
        var mimeString = dataURI
          .split(",")[0]
          .split(":")[1]
          .split(";")[0];
        var ab = new ArrayBuffer(byteString.length);
        var ia = new Uint8Array(ab);
        for (var i = 0; i < byteString.length; i++) {
          ia[i] = byteString.charCodeAt(i);
        }

        //리사이징된 file 객체
        resizedImageFile = new Blob([ab], { type: mimeString });
        console.log(resizedImageFile);
      };
    };
    return resizedImageFile;
  };

  const onSubmit = async () => {
    if (detail.detail == "") {
      window.alert("상세 제품 소개를 입력해주세요.");
      return;
    }
    if (imageFile == null) {
      window.alert("대표 썸네일을 입력해주세요.");
      return;
    }
    if (detailImageFile == null) {
      window.alert("대체 사진들을 입력해주세요.");
      return;
    }
    if (descriptionImageFiles == null) {
      window.alert("상세 사진들을 입력해주세요.");
      return;
    }

    const formData = new FormData();
    formData.append("thumb", imageFile);
    formData.append("detail", detail.detail);

    for (let i = 0; i < detailImageFile.length; i++) {
      formData.append("displaies", detailImageFile[i]);
    }
    for (let i = 0; i < descriptionImageFiles.length; i++) {
      formData.append("images", descriptionImageFiles[i]);
    }

    const res = await api.postOneColor(location.state.colorId, formData);

    if (res.success) {
      window.alert("성공적으로 업로드되었습니다.");
      // window.location.reload();
      history.goBack();
    }
  };

  return (
    <Container fluid>
      <PageTitle
        sm="12"
        title={`${color.Product.title} - ${color.name}`}
        className="text-sm-left"
      />

      <Row>
        <Col md="6" className="form-group">
          <div className="custom-file mb-3">
            <input
              onChange={onFileChange}
              type="file"
              className="custom-file-input"
              id="singleFile"
            />
            <label className="custom-file-label" htmlFor="singleFile">
              대표 이미지를 입력해주세요
            </label>
          </div>
        </Col>
        {image && (
          <Col md="6">
            <img
              style={{ width: 148, height: 221.6 }}
              src={image}
              alt="thumb"
            />
          </Col>
        )}
      </Row>
      <Row>
        <Col md="6" className="form-group">
          <div className="custom-file mb-3">
            <p>제품 대체 이미지 등록</p>
            <input
              className="custom-file-input"
              onChange={onMulitpleFileChange}
              type="file"
              multiple
              id="multipleFile"
            />
            <label className="custom-file-label" htmlFor="multipleFile">
              대체 이미지들을 입력해주세요
            </label>
            <div style={{ display: "flex" }}>
              {detailImages.map((item, idx) => (
                <div
                  key={idx}
                  style={{ display: "flex", position: "relative" }}
                >
                  <img
                    style={{ width: 148, height: 221.6 }}
                    src={item}
                    class="image-preview"
                  />
                </div>
              ))}
            </div>
          </div>
        </Col>
      </Row>
      <Row>
        <Col md="6" className="form-group">
          <div className="custom-file mb-3">
            <p>제품 상세 이미지 등록</p>
            <input
              className="custom-file-input"
              onChange={onMulitpleDetailChange}
              type="file"
              multiple
              id="multipleDescriptionFile"
            />
            <label
              className="custom-file-label"
              htmlFor="multipleDescriptionFile"
            >
              상세 이미지들을 입력해주세요
            </label>
            <div style={{ display: "flex" }}>
              {descriptionImages.map((item, idx) => (
                <div
                  key={idx}
                  style={{ display: "flex", position: "relative" }}
                >
                  <img
                    style={{ width: 148, height: 221.6 }}
                    src={item}
                    class="image-preview"
                  />
                </div>
              ))}
            </div>
          </div>
        </Col>
      </Row>
      <Row noGutters className="page-header py-4">
        <Col lg="12">
          <Card>
            <CardBody>
              <Quill value={detail} setValue={setDetail} />
            </CardBody>
          </Card>
        </Col>
      </Row>
      <Row>
        <Col>
          <Button onClick={onSubmit} className="mr-3">
            저장
          </Button>
          <Button
            onClick={() => {
              history.goBack();
            }}
            theme="secondary"
          >
            취소
          </Button>
        </Col>
      </Row>
    </Container>
  );
}

export default AddProductDetail;
