import useInquiry from "../modules/inquiry/hooks";
import React, { useEffect, useState } from "react";
import {
  Container,
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter
} from "shards-react";

import PageTitle from "../components/common/PageTitle";
import api from "../api/api";
import Pagination from "../components/inquiry/pagination";
import Quill from "../components/Quill";

const Tables = () => {
  const [modalOpen, setModalOpen] = useState();
  const [modalData, setModalData] = useState({ User: {} });
  const [inquiry, setInquiry] = useState([])
  const [reply, setReply] = useState("")

  useEffect(async () => {
    const res = await api.getInquiry(0)
    setInquiry(res);
  }, []);

  const onChange = item => () => {
    setModalData(item);
    if (item.reply) {
      setReply({ detail: item.reply })
    }
    setModalOpen(prev => !prev);
  };

  useEffect(() => {
    console.log(reply)
  }, [reply])



  return (
    <Container fluid className="main-content-container px-4">
      {/* Page Header */}
      <Row noGutters className="page-header py-4">
        <PageTitle
          sm="4"
          title="문의 사항"

          className="text-sm-left"
        />
      </Row>

      {/* Default Light Table */}
      <Row>
        <Col>
          <Card
            small
            className="mb-4"
            style={{ position: "relative", height: "650px" }}
          >
            <CardHeader className="border-bottom">
              <h6 className="m-0">문의 내역</h6>
            </CardHeader>
            <CardBody className="p-0 pb-5">
              <table className="table mb-0" style={{ tableLayout: 'fixed' }}>
                <thead className="bg-light">
                  <tr>
                    <th scope="col" className="border-0" width="6%" style={{ paddingLeft: "30px" }}>
                      #
                    </th>
                    <th scope="col" className="border-0" width="6%">
                      문의자
                    </th>
                    <th scope="col" className="border-0" width="11%">
                      비밀글 여부
                    </th>
                    <th scope="col" className="border-0" width="16%">
                      이메일
                    </th>
                    <th scope="col" className="border-0" width="11%">
                      연락처
                    </th>
                    <th scope="col" className="border-0" width="35%" >
                      문의사항
                    </th>
                    <th scope="col" className="border-0" width="10%">
                      문의일자
                    </th>
                    <th scope="col" className="border-0" width="6%">
                      답변여부
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {inquiry.map((item, index) => (
                    <tr
                      style={{ cursor: "pointer" }}
                      onClick={onChange(item)}
                      key={item.id}
                    >

                      <td style={{ paddingLeft: "30px" }}>{item.id}</td>
                      <td style={{ whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden" }}>{item.User && item.User.name}</td>
                      <td style={{ whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden" }}>{item.isPrivate ? "o" : "x"}</td>
                      <td style={{ whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden" }}>{item.User && item.User.email}</td>
                      <td style={{ whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden" }}>{item.User && item.User.phone}</td>
                      <td>{item.contents}</td>
                      <td style={{ whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden" }}>
                        {item.createdAt}
                      </td>
                      <td>{!!item.reply ? "답변 완료" : "답변 대기 중"}</td>
                    </tr>
                  ))}
                </tbody>
                <Modal open={modalOpen} centered={true}>
                  <ModalHeader className="d-block">
                    문의사항
                    <div
                      onClick={onChange({ User: {} })}
                      style={{
                        cursor: "pointer",
                        position: "absolute",
                        right: "1rem",
                        top: "1rem"
                      }}
                    >
                      <i className="material-icons">close</i>
                    </div>
                  </ModalHeader>
                  <ModalBody>
                    <Row>
                      <Col md="3">
                        <strong className="text-muted">이름</strong>
                      </Col>
                      <p>{modalData.User.name}</p>
                    </Row>
                    <Row>
                      <Col md="3">
                        <strong className="text-muted">비밀글 여부</strong>
                      </Col>
                      <p>{modalData.isPrivate ? "o" : "x"}</p>
                    </Row>
                    <Row>
                      <Col md="3">
                        <strong className="text-muted">이메일</strong>
                      </Col>
                      <p>{modalData.User.email}</p>
                    </Row>
                    <Row>
                      <Col md="3">
                        <strong className="text-muted">연락처</strong>
                      </Col>
                      <p>{modalData.User.phone}</p>
                    </Row>
                    <Row>
                      <Col md="3">
                        <strong className="text-muted">문의사항</strong>
                      </Col>
                      <Col md="9" style={{ paddingLeft: 0 }}>
                        <div dangerouslySetInnerHTML={{ __html: modalData.contents }}></div>
                      </Col>
                    </Row>
                    <Row className="mt-3">
                      <Col>
                        <strong>답변하기</strong>
                        <Quill value={reply} setValue={setReply} placeholder="답변을 입력해주세요." />
                      </Col>
                    </Row>
                  </ModalBody>
                  <ModalFooter>
                    <Button theme="secondary">
                      취소
                    </Button>
                    <Button
                      onClick={async () => {
                        const res = await api.postInuqiryReply(modalData.id, { contents: reply.detail })
                        console.log(res);
                        setInquiry(await api.getInquiry(0))
                        setModalOpen(false)
                      }}
                    >
                      {!!modalData.reply ? "수정하기" : "답변하기"}
                    </Button>

                  </ModalFooter>
                </Modal>
              </table>
            </CardBody>
            <Pagination
              style={{
                display: "flex",
                position: "absolute", bottom: 10,
                justifyContent: 'center'
              }}
            />
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default Tables;
