import React, { useEffect, useRef, useState } from "react";
import {
  Modal,
  ModalBody,
  ModalHeader,
  Container,
  Row,
  Col,
  Button,
  Form,
  CardBody,
  Card,
  FormRadio,
  FormInput
} from "shards-react";

import PageTitle from "../components/common/PageTitle";
import Editor from "../components/add-new-post/Editor";
import { BASE_URL } from "../modules/host";
import api from "../api/api";

const AddNewPost = ({ location, history }) => {
  // 제품 개요 불러오기 
  const [productInfo, setProductInfo] = useState({ categoryId: "1" });
  const [loading, setLoading] = useState(false);

  // 옵션창 보여줄지 말지 결정

  const [isApprove, setIsApprove] = useState(false);


  useEffect(async () => {
    if (location.state) {
      setLoading(true);
      const data = await api.getOneProduct(location.state);
      console.log(data);

      setLoading(false);
      setProductInfo(data);

      setIsApprove(data.hasOption)

    }
  }, []);




  //서버통신
  const onSubmit = async e => {
    e.preventDefault();

    if (
      !productInfo.title ||
      !productInfo.price ||
      !productInfo.sojae ||
      !productInfo.fit ||
      !productInfo.detail ||
      !productInfo.model ||
      !productInfo.setak ||
      !productInfo.categoryId ||
      !productInfo.productCode
    ) {
      window.alert("모든 정보를 기입해주세요!");
      return;
    }
    if (productInfo.id) {
      await api.patchProduct(productInfo.id, productInfo);
    } else {
      const res = await api.addProduct(productInfo);
      console.log(res.id);
      history.push({
        pathname: "write",
        state: res.id
      })
    }
    setIsApprove(true);
  };



  if (loading) {
    return (
      <Container
        fluid
        className="main-content-container px-4 pb-4"
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center"
        }}
      >
        로딩중입니다.
        <i className="material-icons" style={{ fontSize: 50, marginLeft: 10 }}>
          cached
        </i>
      </Container>
    );
  }

  return (
    <Container fluid className="main-content-container px-4 pb-4">
      <Form
        onSubmit={e => {
          e.preventDefault();
          console.log(e);
        }}
        action={`${BASE_URL}news/add`}
        method="post"
      >
        {/* Page Header */}
        <Row noGutters className="page-header py-4">
          <PageTitle sm="4" title="등록하기" className="text-sm-left" />
        </Row>
        <Row>
          {/* Editor */}
          <Col lg="12" md="12">
            <Editor value={productInfo} setValue={setProductInfo} />
          </Col>
        </Row>
        <Row>
          <Col
            className="mb-3"
            style={{ display: "flex", justifyContent: "flex-end" }}
          >
            <Button
              size="sm"
              theme="primary"
              className="col-md-1 mr-3"
              type="submit"
              onClick={onSubmit}
            >
              {productInfo.id > 0 ? "수정" : "저장"}
            </Button>
            {productInfo.id > 0 && (
              <Button
                size="sm"
                id="close"
                theme="danger"
                className="col-md-1"
                onClick={() => {
                  window.confirm("정말 삭제하시겠습니까?") &&
                    api
                      .deleteProduct(productInfo.id)
                      .then(history.push("/products"));
                }}
              >
                삭제
              </Button>
            )}
          </Col>
        </Row>
        {isApprove && <OptionTable history={history} productId={location.state} />}
        {/* <OptionTable history={history} productId={location.state} hasOption={hasOption} setHasOption={setHasOption} /> */}


      </Form>

    </Container>
  );
};

function OptionTable({ productId, history }) {

  const [hasOption, setHasOption] = useState(false);
  const [options, setOptions] = useState(null);
  const [optionModal, setOptionModal] = useState(false);
  const [actionSwitch, setActionSwitch] = useState(false); // 변화 시 다시 불러오기 위한 함수





  //라디오 버튼 제어
  const onChangeOption = e => {
    const {
      target: { value }
    } = e;
    setHasOption(value);
  };


  useEffect(async () => {
    const res = await api.getOneProduct(productId);
    if (!res) {
      return
    }
    setHasOption(res.hasOption)
    setOptions(res.ProductColors);

  }, [productId, actionSwitch])


  return (
    <Row>
      <Col lg="12">
        <Card>
          <CardBody>
            <Row>
              <Col>
                <div>상품옵션</div>
                <FormRadio
                  name="has-option"
                  value={true}
                  onChange={onChangeOption}
                >
                  옵션 선택
                </FormRadio>
                <FormRadio
                  name="has-option"
                  value={false}
                  onChange={onChangeOption}
                >
                  선택 안함
                </FormRadio>

              </Col>
            </Row>
            {hasOption === "true" && <Row>

              <Col>
                <div className="flex justify-end">
                  <Button
                    size="sm"
                    theme="primary"
                    className="col-md-1 mr-3"
                    type="submit"
                    onClick={() => {
                      setOptionModal(true)
                    }}
                  >
                    옵션 추가
                  </Button>
                </div>
                <table className="table mb-0">
                  <thead className="bg-light">

                    <th
                      width="10%"
                      scope="col"
                      className="border-0"
                      style={{ paddingLeft: "30px" }}
                    >
                      색상
                    </th>
                    <th
                      width="50%"
                      scope="col"
                      className="border-0"
                      style={{ paddingLeft: "30px" }}
                    >
                      사이즈
                    </th>

                    <th width="40%" scope="col" className="border-0" />



                  </thead>
                  <tbody>
                    {
                      options &&
                      options.map((item, idx) => (
                        <tr key={'option' + idx}>
                          <td>
                            {item.name}
                          </td>
                          <td>
                            {item.ProductColorSizes.map(item => item.size).join(',')}
                          </td>
                          <td>
                            <div>
                              <Button
                                theme="danger"
                                className="mr-3"
                                onClick={async () => {
                                  await api.deleteOneColor(item.id)
                                  setActionSwitch(prev => !prev)
                                }}
                              >삭제</Button>
                              {/* <Button
                                theme="primary"
                                className="mr-3"
                              >수정</Button> */}
                              <Button
                                theme="secondary"
                                onClick={() => {
                                  history.push({
                                    pathname: "add-products-detail",
                                    state: { colorId: item.id }
                                  })
                                }}
                              >상품 세부사항 등록</Button>
                            </div>
                          </td>
                        </tr>

                      ))
                    }
                  </tbody>
                </table>
              </Col>
            </Row>}
          </CardBody>
        </Card>
      </Col>
      <OptionModal
        optionModal={optionModal}
        setOptionModal={setOptionModal}
        productId={productId}
        setActionSwitch={setActionSwitch}
      />
    </Row>
  )
}

function OptionModal({
  productId,
  optionModal,
  setActionSwitch,
  setOptionModal }) {
  const [optionValue, setOptionValue] = useState({ seksangList: [] });

  const onChangeOptionValue = (e) => {
    const { name, value: nValue } = e.target;
    const newValue = {
      ...optionValue,
    }
    newValue[name] = nValue;
    setOptionValue(newValue)
    console.log(optionValue)
  }

  const optionId = useRef(1);

  const changeSizeCount = (id, e) => {
    const { value } = e.target;
    setOptionValue({
      ...optionValue,
      seksangList: optionValue.seksangList.map((item) => {
        if (item.id === id) {
          return {
            ...item,
            count: value
          }
        } else {
          return item
        }
      })
    })
  }
  useEffect(() => {
    setOptionValue({ seksangList: [] })
  }, [optionModal])

  const onSubmit = async () => {

    const data = await api.addProductColor();
  }

  return (
    <Modal size="lg" open={optionModal} centered={true}>
      <ModalHeader className="d-block">옵션추가</ModalHeader>
      <ModalBody>
        <Form>
          <label htmlFor="name">색상</label>
          <FormInput
            id="name"
            type="text"
            name="name"
            placeholder="색상을 입력하세요."
            value={optionValue.name}
            onChange={onChangeOptionValue}
          />
          {/* <Quill
            value={quillDetail}
            setValue={setQuillDetail}
            placeholder="제품 설명을 입력해주세요."
          /> */}
          <label htmlFor="seksangSize">사이즈</label>
          <FormInput
            id="seksangSize"
            type="text"
            className="mb-3"
            name="seksangSize"
            placeholder="사이즈를 입력하세요. ;또는 엔터 로 구분됩니다."
            value={optionValue.seksangSize}
            onChange={onChangeOptionValue}
            onKeyUp={(e) => {
              if (e.key === 'Enter') {
                optionValue.seksangList.push({ id: optionId.current, size: optionValue.seksangSize, count: 0 });
                optionId.current += 1;
                setOptionValue({
                  ...optionValue,
                  seksangSize: ""
                })
              }
            }}

          />



        </Form>
        <table className="table mb-0">
          <tr>
            <th>
              사이즈
            </th>
            <th>
              재고 수량
            </th>
            <th />
          </tr>
          <tbody>
            {
              optionValue.seksangList.map((item) => {
                return (
                  <tr>
                    <td>
                      {item.size}
                    </td>
                    <td>
                      <FormInput
                        onChange={(e) => changeSizeCount(item.id, e)}
                        value={item.count}
                      />
                    </td>
                    <td>
                      <Button
                        size="sm"
                        theme="danger"
                        onClick={() => {
                          setOptionValue({
                            ...optionValue,
                            seksangList: optionValue.seksangList.filter(size => size.id !== item.id)
                          })
                        }}
                      >
                        삭제
                      </Button>
                    </td>
                  </tr>
                )
              })
            }
          </tbody>
        </table>

        <Button
          size="sm"
          theme="secondary"
          className="col-md-1 mr-3"
          onClick={() => {
            setOptionModal(false)
          }}
        >
          취소
        </Button>
        <Button
          size="sm"
          theme="primary"
          className="col-md-1 mr-3"
          onClick={async () => {


            const res = await api.addProductColor(productId, optionValue)
            if (res.success) {
              window.alert('옵션이 정상적으로 등록되었습니다.')
              setOptionModal(false)
              setActionSwitch(prev => !prev)
            } else {
              window.alert('옵션 저장에 실패하였습니다.')
            }
          }}
        >
          저장
        </Button>



      </ModalBody>
    </Modal>
  )
}

// const AddNewPost = () => (
// );

export default AddNewPost;
