import React, {
    useEffect,
    useState,
    useMemo,
    useRef,
    useCallback
} from "react";
import {
    Container,
    Row,
    Col,
    Card,
    CardHeader,
    CardBody,
    Modal,
    Form,
    InputGroup,
    InputGroupAddon,
    InputGroupText,
    FormInput,
    Button,
    ModalBody,
    ModalHeader,
    ModalFooter
    // Button
} from "shards-react";
import { NavLink as RouteNavLink } from "react-router-dom";
import { NavLink } from "shards-react";
import PageTitle from "../components/common/PageTitle";
import api from "../api/api";

const CalendarTable = React.memo(({ calendar }) => {
    console.log(calendar)


    return (
        <table className="table mb-0">
            <thead className="bg-light">
                <tr>
                    <th
                        width="5%"
                        scope="col"
                        className="border-0"
                        style={{ paddingLeft: "30px" }}
                    >
                        #
                    </th>
                    <th width="45%" scope="col" className="border-0">
                        일정
                    </th>
                    <th width="15%" scope="col" className="border-0">
                        시작일
                    </th>
                    <th width="15%" scope="col" className="border-0">
                        종료일
                    </th>
                </tr>
            </thead>
            <tbody>
                {calendar.map((item, index) => (
                    <tr key={item.id}>
                        <td style={{ paddingLeft: "30px" }}>{item.id}</td>
                        {/* <td>{item.title}</td> */}
                        <td>
                            {item.title.slice(0, 40) + (item.title.length > 40 ? "..." : "")}
                        </td>
                        <td>{item.start}</td>
                        <td>{item.end}</td>
                    </tr>
                ))}
            </tbody>
        </table>
    );
});

const BlogPosts = ({ history }) => {

    const [open, setOpen] = useState(false);
    const [plan, setPlan] = useState({})
    const [calendar, setCalendar] = useState([]);

    useEffect(async () => {
        const res = await api.getCalendar();
        setCalendar(res);
    }, []);

    const onClick = () => {
        setOpen(prev => !prev)
    }

    const onChange = (e) => {
        const { name, value } = e.target;
        setPlan((prev) => {
            const newPlan = { ...prev }
            newPlan[name] = value
            return newPlan
        })

    }

    return (
        <Container fluid className="main-content-container px-4">
            {/* Page Header */}
            <Row
                noGutters
                className="page-header py-4"
                style={{ position: "relative" }}
            >
                <PageTitle sm="4" title="달력 관리" className="text-sm-left" />
                <div
                    className="bg-primary text-white text-center rounded outline ml-auto"
                    style={{
                        display: "flex",
                        alignItems: "center",
                        // marginTop: 10,
                        cursor: 'pointer',
                        padding: "8px 15px",
                        marginRight: 10,
                        position: "absolute",
                        right: "0px"
                    }}
                    onClick={onClick}
                >
                    <div className="text-white">상품 등록</div>

                </div>
            </Row>

            {/* Default Light Table */}
            <Row>
                <Col>
                    <Card
                        small
                        className="mb-4"
                        style={{ position: "relative", height: "107%" }}
                    >
                        <CardHeader
                            className="border-bottom"
                            style={{ display: "flex", alignItems: "baseline" }}
                        >
                            <h6 className="m-0" style={{ flexGrow: 1, flexBasis: "100px" }}>
                                전체
                            </h6>
                        </CardHeader>
                        <CardBody className="p-0 pb-5">
                            <CalendarTable calendar={calendar} setCalendar={setCalendar} history={history} />
                            {/* <Pagination
                style={{
                  display: "flex",
                  position: "absolute", bottom: 10,
                  justifyContent: 'center'
                }}
              /> */}
                        </CardBody>
                    </Card>
                </Col>
            </Row>
            <Modal size="lg" open={open} centered={true}>
                <ModalHeader>일정 등록</ModalHeader>
                <ModalBody>
                    <label htmlFor="title">일정명</label>
                    <FormInput
                        type="text"
                        id="title"
                        value={plan.title}
                        name="title"
                        onChange={onChange}
                    />
                    <label htmlFor="start">시작일</label>
                    <FormInput
                        type="date"
                        value={plan.start}
                        name="start"
                        id="start"
                        onChange={onChange}
                    />
                    <label htmlFor="end">종료일</label>
                    <FormInput
                        type="date"
                        value={plan.end}
                        name="end"
                        name="end"
                        onChange={onChange}
                    />


                </ModalBody>
                <ModalFooter>
                    <Button
                        theme="secondary"
                        onClick={() => { setOpen(false) }}
                    >
                        취소
                    </Button>
                    <Button
                        onClick={async () => {
                            const res = await api.postCalendar(plan)
                            console.log(res);
                            setOpen(false)
                            setCalendar(await api.getCalendar())

                        }}
                    >
                        등록

                    </Button>
                </ModalFooter>
            </Modal>
        </Container>
    );
};

// export default Tables;
export default React.memo(BlogPosts);
