import axios from "axios";


// export const BASE_URL = "http://localhost:8000/"
export const BASE_URL = "./"

const _baseGetRequest = async (path) => {
    const res = await axios.get(BASE_URL + path)
    return res.data;
}

const _basePostRequest = async (path, param) => {
    const res = await axios.post(BASE_URL + path, param)
    return res.data;
}
const _basePatchRequest = async (path, param) => {
    const res = await axios.patch(BASE_URL + path, param)
    return res.data;
}

const _baseDeleteRequest = async (path) => {
    const res = await axios.delete(BASE_URL + path)
    return res.data
}

const addProduct = async (param) => {
    const res = await _basePostRequest('admin/product', param)
    return res.data
}

const addProductColor = async (productId, param) => {
    const res = await _basePostRequest(`admin/product/${productId}/color`, param)
    return res
}

const getProducts = async (param) => {
    const res = await _baseGetRequest('admin/product')
    return res.data
}

const getOneProduct = async (produtId) => {
    const res = await _baseGetRequest(`admin/product/${produtId}`)
    return res.data
}

// 색상 불러오기
const getOneColor = async (colorId) => {
    const res = await _baseGetRequest(`admin/product/color/${colorId}`)
    return res.data
}
// 색상 등록하기
const postOneColor = async (colorId, param) => {
    const res = await _basePostRequest(`admin/product/color/${colorId}`, param)
    return res
}

const deleteOneColor = async (colorId) => {
    const res = await _baseDeleteRequest(`admin/product/color/${colorId}`)
    return res
}

const patchProduct = async (productId, param) => {
    const res = await _basePatchRequest(`admin/product/${productId}`, param)
    return res.data
}

const deleteProduct = async (productId, param) => {
    const res = await _baseDeleteRequest(`admin/product/${productId}`, param)
    return res.data
}


//달력 등록

//달력 가져오기
const getCalendar = async () => {
    const res = await _baseGetRequest('calendar')
    return res.data
}
//달력 등록하기
const postCalendar = async (param) => {
    const res = await _basePostRequest('calendar', param)
    return res.data
}

//문의사항 관리
//문의사항 가져오기
const getInquiry = async (page) => {
    const res = await _baseGetRequest(`inquiry?page=${page}`)
    return res.data;
}
// 문의사항 답변하기
const postInuqiryReply = async (inquiryId, param) => {
    const res = await _basePostRequest(`inquiry/${inquiryId}`, param)
    return res.data;
}

// 파트너 관리
// 파트너 가져오기
const getPartners = async () => {
    const res = await _baseGetRequest("partner");
    return res.data
}
//파트너 등록하기
const postPartners = async (param) => {
    const res = await _basePostRequest('partner', param)
    return res.data
}
//파트너 수정하기
const patchPartner = async (id, param) => {
    const res = await _basePatchRequest(`partner/${id}`, param)
    return res.data
}


export default {
    addProduct,
    getProducts,
    addProductColor,
    getOneProduct,
    deleteProduct,
    patchProduct,
    // 색상 관리 
    getOneColor,
    postOneColor,
    deleteOneColor,
    //달력 관리
    getCalendar,
    postCalendar,
    //문의사항 관리
    getInquiry,
    postInuqiryReply,
    //파트너 관리
    getPartners,
    postPartners,
    patchPartner,


}